import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import PettyCashUpdateForm from "./updateForm";
import PettyCashDocUpdateForm from "./updateDocForm";
import History from "./history";


import notification from "../../../../components/notification";
import { Spin, Tooltip } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/financial/pettyCash/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";
import Print from "./print";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/financials/petty-cashes";


class UpdatePettyCash extends Component {

  state = {
    visible: false
  }
  // Get All data diawal
  componentDidMount() {
    const { editPettyCash } = this.props;
    const { id } = this.props.match.params;
    editPettyCash(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.pettyCash.error) {
      notification("error", this.props.pettyCash.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.pettyCash.success) {
      notification("success", this.props.pettyCash.success);
      this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { updatePettyCash } = this.props;
    const { id } = this.props.match.params;

    updatePettyCash(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelPettyCash } = this.props;
    const { id } = this.props.match.params;

    cancelPettyCash(id, value);
  }

  handleModalHistory = () => {
    this.setState({ visible: !this.state.visible })
  };



  render() {
    // console.log(this.props.pettyCash.petty_cash);
    const { petty_cash, loading } = this.props.pettyCash
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="pettyCashes.update.title" />}
            style={{ margin:0 }}
          >
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={petty_cash} />

            <Button 
              style={{ marginLeft: "5px" }} 
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>


          <History
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={petty_cash ? petty_cash.description : null}
          />
          
          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="pettyCashes.tab.detail" />} 
              key="1" 
            >
              {this.props.pettyCash.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <PettyCashUpdateForm
                  submit={this.handleSubmit}
                  pettyCash={this.props.pettyCash}
                  permissions={permissions}
                  processPettyCash={this.props.processPettyCash}
                  handleSubmitCancel={this.handleSubmitCancel}
                />    
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="pettyCashes.tab.attachment" />} 
              key="2"
            >  
             {this.props.pettyCash.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <PettyCashDocUpdateForm
                  submit={this.handleSubmit}
                  pettyCash={this.props.pettyCash}
                  downloadDocument={this.props.downloadDocument}
                />    
              )}
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { 
    pettyCash: state.pettyCash, 
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdatePettyCash);
