import React, { Component } from "react";
import moment from "moment"
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Col, InputNumber, DatePicker, Popconfirm, Steps, Row, Radio } from "antd";
import { customDot } from "../../../App/app.style";
import ModalCancel from "./modalCancel";
import getLanguage from "../../../App/MyLanguange";


const { Option } = Select;
// const InputGroup = Input.Group;
const TextArea = Input.TextArea;
const Step = Steps.Step;

class PettyCashUpdateForm extends Component {
  state = {
    check: 2,
    visibleModalCancel: false,
  }

  handleModalCancel = () => {
    this.setState({
      visibleModalCancel: !this.state.visibleModalCancel
    })
  }

  handleChangePettyCashType = (value) => {
    const { setFieldsValue } = this.props.form;
    const { petty_cash_types } = this.props.pettyCash;

    petty_cash_types.map((petty_cash_type) => {
      if (petty_cash_type.id_petty_cash_type === value) {
        setFieldsValue({
          transaction_type: petty_cash_type.transaction_type
        })
      }
    })

  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      // }
      if (!err) {
        values.transaction_date = values.transaction_date.format("YYYY-MM-DD HH:mm:ss");
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);

    const { getFieldDecorator } = this.props.form;
    const { pettyCash, processPettyCash, } = this.props;
    const { loading } = pettyCash;

    const data = pettyCash.petty_cash ? pettyCash.petty_cash : [];

    let permissions = this.props.permissions;

    // const error = pettyCash.errorField ? pettyCash.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "100%"
    }

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    }

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalCancel
          visible={this.state.visibleModalCancel}
          handleModal={this.handleModalCancel}
          submit={this.props.handleSubmitCancel}
          loading={loading}
        />
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
           <IntlMessages id="pettyCashes.add.subtitle.data" />
         </Divider> */}
        <Row style={{ margin: "24px 0px" }} >
          <Col span={16} justify="center">
            <Steps size="small"
              progressDot={customDot}
              current={
                data.status - 1
              }
            >
              <Step title={getLanguage({id: "open"})} />
              <Step title={getLanguage({id: "processed"})} />
              <Step title={getLanguage({id: "cancelled"})} />
            </Steps>
          </Col>
        </Row>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="pettyCashes.no" />}
            >
              {getFieldDecorator("petty_cash_no", {
                initialValue: data.petty_cash_no,

              })(<Input
                disabled={true}
              />)}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={
                <IntlMessages id="purchaseInvoices.transactionDate" />
              }
            //style={{ marginBottom: 0, paddingBottom: 0 }}
            >
              {getFieldDecorator("transaction_date", {
                initialValue: moment(
                  data.transaction_date,
                  "YYYY-MM-DD HH:mm:ss"
                )
              })(
                <DatePicker
                  style={{ width: "100%" }}
                  showTime
                  format="DD MMMM YYYY HH:mm:ss"
                  // onChange={this.handleChangeTransDate}
                  onOpenChange={this.handleChangeTransDate}
                  disabled={
                    permissions.includes("Can Back Date") && data.status === 1
                      ? false
                      : true
                  }
                  allowClear={false}
                  disabledDate={e => e.valueOf() > moment()}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="pettyCashType" />}
            >
              {getFieldDecorator("id_petty_cash_type", {
                initialValue: data.id_petty_cash_type,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={this.props.pettyCash.loading}
                  showSearch
                  optionFilterProp="children"
                  onChange={this.handleChangePettyCashType}
                  disabled={data.status === 1 ? false : true}
                >
                  {pettyCash.petty_cash_types.map((data, key) => (
                    <Option key={key} value={data.id_petty_cash_type}>
                      {data.petty_cash_type_code + " (" + data.description + ")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="transactionType" />}
            >
              {getFieldDecorator("transaction_type", {
                initialValue: data.transaction_type,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Radio.Group
                  buttonStyle="solid"
                  disabled={true}
                  style={radioGroupStyle}
                >
                  <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="deposit" /></Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={2}><IntlMessages id="withdrawal" /></Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="amount" />}
            >
              {getFieldDecorator("amount", {
                initialValue: data.amount,
                // initialValue: 0, 
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <InputNumber
                  formatter={value =>
                    `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                  }
                  parser={value => value.replace(/\$\s?|(,*)/g, "")}
                  min={0.01}
                  precision={2}
                  disabled={data.status === 1 ? false : true}
                  style={{ width: "100%" }}
                />
              )}
            </Form.Item>

          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="locations.pageHeader" />}
            >
              {getFieldDecorator("id_location", {
                initialValue: data.id_location,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={this.props.pettyCash.loading}
                  showSearch
                  optionFilterProp="children"
                  disabled={data.status === 1 ? false : true}
                >
                  {pettyCash.locations.map((data, key) => (
                    <Option key={key} value={data.id_location}>
                      {data.location_code + " (" + data.location_name + ")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="employees.pageHeader" />}
            >
              {getFieldDecorator("id_employee", {
                initialValue: data.id_employee,
                // rules: [
                //   {
                //     required: true,
                //     message: <IntlMessages id="required" />
                //   }
                // ]
              })(
                <Select
                  loading={this.props.pettyCash.loading}
                  showSearch
                  optionFilterProp="children"
                  allowClear={true}
                  disabled={data.status === 1 ? false : true}
                >
                  {pettyCash.employees.map((data, key) => (
                    <Option key={key} value={data.id_employee}>
                      {data.employee_code + " (" + data.first_name + (data.last_name ? " " + data.last_name : "") + ")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="referenceNo" />}
            >
              {getFieldDecorator("reference_no", {
                initialValue: data.reference_no,

              })(<Input
                disabled={data.status === 1 ? false : true}
              />)}
            </Form.Item>


            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="note" />}
            >
              {getFieldDecorator("note", {
                initialValue: data.note,
              })(<TextArea
                autosize={{ minRows: 3 }}
                disabled={data.status === 1 ? false : true}
              />)}
            </Form.Item>


          </div>
        </div>


        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginRight: "5px" }}
            loading={loading}
            disabled={
              data.status === 1
                ? false
                : true
            }
          >
            <IntlMessages id="button.submit" />
          </Button>

          {/* Process */}
          <Popconfirm
            title={<IntlMessages id="confirmTransaction" />}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
            onConfirm={() =>
              processPettyCash(data.id_petty_cash)
            }
          >
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.status === 1 &&
                  permissions.includes("Confirm Petty Cash")
                  ? false
                  : true
              }
              loading={loading}
            >
              <IntlMessages id="button.confirmTransaction" />
            </Button>
          </Popconfirm>

          {/* Cancel */}
          {/* <Popconfirm
            title={<IntlMessages id="confirmCancelTransaction" />}
            onConfirm={() =>
              cancelPettyCash(data.id_petty_cash)
            }
          > */}
          <Button
            style={{ marginRight: "5px", padding: "0px 15px" }}
            disabled={
              data.status === 2 &&
                permissions.includes("Cancel Petty Cash")
                ? false
                : true
            }
            loading={loading}
            onClick={this.handleModalCancel}
          >
            <IntlMessages id="button.cancelTransaction" />
          </Button>
          {/* </Popconfirm> */}

        </Form.Item>
      </Form>
    );
  }
}

const WrappedPettyCashUpdateForm = Form.create({ name: "update_petty_cash" })(PettyCashUpdateForm);

export default WrappedPettyCashUpdateForm;
